import './assets/App.css';
import {ThemeProvider} from '@material-ui/core/styles'
import theme from './components/TemaConfig'
import Navbar from './components/Navbar/Navbar'
import OcultarNavbar from './components/Navbar/OcultarNavbar';
import StickyFooter from './components/Footer/Footer';
import {
  BrowserRouter as Router,
  Route,
} from "react-router-dom";
// import SignIn from './components/Sign-in/SingIn';
// import SingUp from './components/Sing-up/SingUp'
// import SingUp3 from './components/Sing-up/Singup3'

import Home from './components/Home/Home';
import SignUp from './components/SignUp/SignUp'
import Login from './components/Login/Login';
import { AuthProvider } from "./components/Auth";
import PrivateRoute from "./components/PrivateRoute";
import Desaparecidos from './components/Desaparecidos/Desaparecidos'
import FormularioDesaparecidos from './components/Desaparecidos/FormularioDesaparecidos'
import ConsumirAPI from './components/Desaparecidos/ConsumirAPI';
import AboutUs from './components/AboutUs/AboutUs';
import Administracion from './components/Admin/Administración';
import Usuario from './components/Usuario/Usuario';


function App() {
  return (
    <ThemeProvider theme={theme}>
        <AuthProvider>
        <Router>
        <Navbar/>
          <div>
            <Route exact path="/findYouComit/" component={Home} />
            <PrivateRoute exact path="/findYouComit/missing/form" component={FormularioDesaparecidos}/>
            <Route exact path="/findYouComit/login"  component={Login}/>
            <Route exact path="/findYouComit/signup"  component={SignUp}/>
            <Route exact path="/findYouComit/missing" component={Desaparecidos}/>
            <Route exact path="/findYouComit/about-us" component={AboutUs}/>
            <Route exact path="/findYouComit/administration" component={Administracion} />
            <Route exact path="/findYouComit/user" component={Usuario}/>
          </div>
        <StickyFooter/>
        </Router>
      </AuthProvider>
      <OcultarNavbar />
    </ThemeProvider>
  );
}

export default App;
